.text-animate{
    display: inline-block;
    opacity: 0;
    animation: bounceIn 1s 1s;
    animation-fill-mode: forwards;
    min-width: 10px;
}

.text-animate-hover{
    min-width: 10px;
    display: inline-block;
    animation-fill-mode: both;

    &:hover {
        animation: rubberBand 1s;
        color: #C6E070;
    }
}

@for $i from 11 through 41 {
    .text-animate._#{$i} {
        animation-delay: #{$i / 10}s;
    }
}